<template>
		<view class="bgc fontS40" >
			<view class="status_bar"  :style="{'height':tabh+'rpx'}"></view>
			<view class="head flexJBC" v-if="title || tab" >
				<view class="leftButton ">
					<u-icon name="arrow-left"  size="28"  @click="back" v-if="tab" color='#0F172C' ></u-icon> 
					<view class="title fontS40 fw7" v-if="title">
						{{title}}
					</view>
				</view>
				<view class="flex flexic">
					<slot></slot>
				</view>
			</view>
		</view>
</template>

<script>
export default {
	data() {
		return {
			index: 1,
			tabh:'',
			keyword:"",
			
		}
	},
	created() {
		uni.getSystemInfo({
			success:(res)=>{
				let tabh1= res.statusBarHeight
				let deviceWidth=uni.getSystemInfoSync().windowWidth;
				this.tabh = (750 / deviceWidth) * Number(tabh1)  + 2
			}
		}) 
		
	},
  props:{
		title:{
			type:String
		},
		tab:{ 
			type:Boolean,
			default:true
		},
	},
	methods:{
		back(){
			uni.navigateBack()
		},
	}
}
</script>

<style lang="scss" scoped>
	.myimg{
		border: 2rpx dotted #676767;
	}
	.bgc{
		background-color: #F4F5F7;
		// background:linear-gradient(to right ,rgba(#ECE1FF,.1) ,#F4F5F7);
		position: sticky;top:0;left: 0;z-index: 99;
	}
	.head {   
		padding:0rpx 40rpx;
		height: 80rpx; 
		line-height: 80rpx;
		box-sizing: border-box;
		position: sticky;
		color: #0F172C;
	}
	.leftButton{
		display: flex;
		align-items: center;
		.title{
			margin-left: 14rpx;
		}
	}
	.rightboxsearch{
		width: 494rpx;
		height: 68rpx;
		background: #676767;
		box-shadow: 0px 6rpx 20rpx 1rpx rgba(0, 0, 0, 0.2);
		border-radius: 49rpx;
		opacity: 1;
	}
</style>
